/* Styling for input field */

.group-form{
  display: flex;
}
input {
    width: 15%;
    padding: 12px 20px;
    margin: 8px 5px;
    display: inline-block;
    border: 1px solid #ccc;
    border-radius: 4px;
    box-sizing: border-box;
    font-size: 16px;
    
  }
  
  button {
    background-color: #4CAF50;
    color: white;
    padding: 12px 20px;
    margin: 8px 5px;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    font-size: 16px;
  }
  
  button:hover {
    background-color: #45a049;
  }
  
  .group-label {
    width: 80px; /* Adjust this width as needed */
    font-weight: bold;
    margin-right: 10px; /* Add some space between the label and inputs */
  }
